import { useLocation, useNavigate } from 'react-router-dom';

import 'react-datepicker/dist/react-datepicker.css';
import { useState, useEffect, useRef } from 'react';
import 'assets/css/batteriesAddOrUpdate.css';
import { checkEmail, checkPhoneNum } from 'app/utils/validation';
import { useDispatch, useSelector } from 'react-redux';
import { showMessage } from 'app/store/fuse/messageSlice';
import { DetailTitleBar } from 'app/theme-layouts/mainLayout/components';
import { open, close } from 'app/store/common/loadingWrap';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import classnames from 'classnames';
import { selectComboBox } from 'app/store/common/comboBox';
import { selectAdmins, setAdmins } from 'app/store/common/admins';

function AddOrUpdate() {
  const dispatch = useDispatch();
  const companyComboBox = useSelector(selectComboBox).company;
  const projectComboBox = useSelector(selectComboBox).project;
  const departmentComboBox = useSelector(selectComboBox).department;
  const roleComboBox = useSelector(selectComboBox).role;
  const [companyCategory, setCompanyCategory] = useState([]);
  const [projectCategory, setProjectCategory] = useState([]);
  const [departmentCategory, setDepartmentCategory] = useState([]);
  const [roleCategory, setRoleCategory] = useState([]);
  const [companyId, setCompanyId] = useState('');
  const [role, setRole] = useState('');
  const [deptId, setDeptId] = useState('');
  const [projectId, setProjectId] = useState('');
  const navigate = useNavigate();
  const location = useLocation();
  const param = location.state;
  const userId = useRef();
  const userName = useRef();
  const phone = useRef();
  const desc = useRef();
  const useYn = useRef();
  const [validation, setValidation] = useState([]);
  const dataList = useSelector(selectAdmins).admins;

  const saveBtnClick = () => {
    confirmAlert({
      title: `운영자 정보를 ${param.detailLink[0].title} 하시겠습니까?`,
      buttons: [
        {
          label: '예',
          onClick: () => {
            const adminObj = {};
            if (!checkEmail(userId.current.value)) {
              dispatch(
                showMessage({ message: '사용자 ID 이메일 형식을 확인해주세요', variant: 'error' })
              );
              return;
            }
            if (!checkPhoneNum(phone.current.value)) {
              dispatch(showMessage({ message: '연락처 형식을 확인해주세요', variant: 'error' }));
              return;
            }

            adminObj.companyId = companyId;
            adminObj.projectId = projectId;
            adminObj.deptId = deptId;
            adminObj.userId = userId.current.value;
            adminObj.username = userName.current.value;
            adminObj.phone = phone.current.value;
            adminObj.role = role;
            adminObj.userDesc = desc.current.value;
            adminObj.useYn = useYn.current.value;

            const today = new Date();
            const year = today.getFullYear();
            const month = `0${today.getMonth() + 1}`.slice(-2);
            const day = `0${today.getDate()}`.slice(-2);
            const dateString = `${year}-${month}-${day}`;
            const hours = `0${today.getHours()}`.slice(-2);
            const minutes = `0${today.getMinutes()}`.slice(-2);
            const seconds = `0${today.getSeconds()}`.slice(-2);
            const timeString = `${hours}:${minutes}:${seconds}`;
            const clone = dataList.slice();
            const filter = clone.filter((obj) => obj.userId !== param.userId);
            dispatch(open());
            switch (param.detailLink[0].title) {
              case '추가':
                clone.push({
                  companyId,
                  deptId,
                  deptName: departmentComboBox.filter((obj) => obj.code === deptId)[0].codeName,
                  projectId,
                  userId: userId.current.value,
                  username: userName.current.value,
                  role,
                  roleName: roleComboBox.filter((obj) => obj.code === role)[0].codeName,
                  email: userId.current.value,
                  phone: phone.current.value,
                  useYn: useYn.current.value,
                  updateUsername: '한빛 관리자',
                  updateTime: `${dateString} ${timeString}`,
                });
                dispatch(close());
                dispatch(setAdmins({ data: clone }));
                dispatch(
                  showMessage({ message: '운영자 정보가 추가되었습니다.', variant: 'success' })
                );
                navigate('/bss_server/systemManagement/admin');
                break;
              case '수정':
                filter.push({
                  companyId,
                  deptId,
                  deptName: departmentComboBox.filter((obj) => obj.code === deptId)[0].codeName,
                  projectId,
                  userId: param.userId,
                  username: userName.current.value,
                  role,
                  roleName: roleComboBox.filter((obj) => obj.code === role)[0].codeName,
                  email: userId.current.value,
                  phone: phone.current.value,
                  useYn: useYn.current.value,
                  updateUsername: '한빛 관리자',
                  updateTime: `${dateString} ${timeString}`,
                });
                dispatch(close());
                dispatch(setAdmins({ data: filter }));
                dispatch(
                  showMessage({ message: '운영자 정보가 수정되었습니다.', variant: 'success' })
                );
                navigate('/bss_server/systemManagement/admin');
                break;
              default:
            }
          },
        },
        {
          label: '취소',
        },
      ],
    });
  };

  const deleteBtnClick = () => {
    confirmAlert({
      title: `운영자 정보를 삭제 하시겠습니까?`,
      buttons: [
        {
          label: '예',
          onClick: () => {
            dispatch(open());
            const clone = dataList.slice();
            const filter = clone.filter((obj) => obj.userId !== param.userId);
            dispatch(close());
            dispatch(setAdmins({ data: filter }));
            dispatch(showMessage({ message: '운영자 정보가 삭제되었습니다.', variant: 'success' }));
            navigate('/bss_server/systemManagement/admin');
          },
        },
        {
          label: '취소',
        },
      ],
    });
  };

  useEffect(() => {
    setCompanyCategory(companyComboBox);
    setRoleCategory(roleComboBox);
    if (param.detailLink[0].title === '추가') {
      setCompanyId(companyComboBox[0].code);
      setRole(roleComboBox[0].code);
      const projectArr = projectComboBox.filter((obj) => obj.parentId === companyComboBox[0].code);
      setProjectCategory(projectArr);
      setProjectId(projectComboBox[0].code);
      setDepartmentCategory(
        departmentComboBox.filter((obj) => obj.parentId === projectArr[0].code)
      );
      setDeptId(departmentComboBox[0].code);
    } else if (param.detailLink[0].title === '수정') {
      setCompanyId(param.companyId);
      setRole(param.role);
      setProjectCategory(projectComboBox.filter((obj) => obj.parentId === param.companyId));
      setProjectId(param.projectId);
      setDepartmentCategory(departmentComboBox.filter((obj) => obj.parentId === param.projectId));
      setDeptId(param.deptId);
    }
  }, [param.detailLink[0].title, param.companyId, param.projectId, param.role, param.deptId]);

  return (
    <div className="section__grid__wrap content batteries__add__update admin__manage">
      <DetailTitleBar
        flag={param.detailLink[0].title}
        deleteBtnClick={deleteBtnClick}
        saveBtnClick={saveBtnClick}
      />
      {/* input__wrap */}
      <section className="input__wrap">
        <div className="section__inner">
          <ul className="selector__wrap">
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  회사명
                </dt>
                <dd>
                  <select
                    value={companyId}
                    className="custom__select"
                    onChange={(e) => {
                      setCompanyId(e.target.value);
                      setProjectCategory(
                        projectComboBox.filter((obj) => obj.parentId === e.target.value)
                      );
                      setProjectId(projectCategory[0].code);
                      setDepartmentCategory(
                        departmentComboBox.filter((obj) => obj.parentId === projectCategory[0].code)
                      );
                      setDeptId(departmentCategory[0].code);
                    }}
                  >
                    {companyCategory.map((obj, idx) => (
                      <option key={obj.code} value={obj.code}>
                        {obj.codeName}
                      </option>
                    ))}
                  </select>
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  프로젝트명
                </dt>
                <dd>
                  <select
                    value={projectId}
                    className="custom__select"
                    onChange={(e) => {
                      setProjectId(e.target.value);
                      setDepartmentCategory(
                        departmentComboBox.filter((obj) => obj.parentId === e.target.value)
                      );
                      setDeptId(departmentCategory[0].code);
                    }}
                  >
                    {projectCategory.map((obj, idx) => (
                      <option key={obj.code} value={obj.code}>
                        {obj.codeName}
                      </option>
                    ))}
                  </select>
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  부서명
                </dt>
                <dd>
                  <select
                    value={deptId}
                    className="custom__select"
                    onChange={(evt) => {
                      setDeptId(evt.target.value);
                    }}
                  >
                    {departmentCategory.map((obj, idx) => (
                      <option key={obj.code} value={obj.code}>
                        {obj.codeName}
                      </option>
                    ))}
                  </select>
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  사용자ID
                </dt>
                <dd>
                  <input
                    defaultValue={param.userId}
                    type="email"
                    className={classnames('custom__input', [
                      { readonly: param.detailLink[0].title === '수정' },
                    ])}
                    readOnly={param.detailLink[0].title === '수정'}
                    ref={userId}
                  />
                  {validation.map(
                    (obj) =>
                      obj.type === 'userId' && (
                        <p className="f__medium field__error">{obj.message}</p>
                      )
                  )}
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  사용자명
                </dt>
                <dd>
                  <input
                    defaultValue={param.username}
                    type="text"
                    className="custom__input"
                    ref={userName}
                  />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  연락처
                </dt>
                <dd>
                  <input
                    defaultValue={param.phone}
                    type="text"
                    className="custom__input"
                    ref={phone}
                  />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  권한
                </dt>
                <dd>
                  <select
                    value={role}
                    className="custom__select"
                    onChange={(evt) => {
                      setRole(evt.target.value);
                    }}
                  >
                    {roleCategory.map((obj, idx) => (
                      <option key={obj.code} value={obj.code}>
                        {obj.codeName}
                      </option>
                    ))}
                  </select>
                </dd>
              </dl>
            </li>
            <li className="w50">
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  사용여부
                </dt>
                <dd>
                  <select className="custom__select" defaultValue={param.useYn} ref={useYn}>
                    <option value="1">사용</option>
                    <option value="0">미사용</option>
                  </select>
                </dd>
              </dl>
            </li>
            <li className="w100">
              <dl>
                <dt>설명</dt>
                <dd>
                  <textarea className="custom__input" ref={desc} />
                </dd>
              </dl>
            </li>
          </ul>
        </div>
      </section>
      {/* // input__wrap */}
    </div>
  );
}

export default AddOrUpdate;
