const checkPhoneNum = (phoneTxt) => {
  return (
    /^[0-9]{2}-[0-9]{3}-[0-9]{4}$/.test(phoneTxt) || // xx - xxx - xxxx
    /^[0-9]{2}-[0-9]{4}-[0-9]{4}$/.test(phoneTxt) || // xx - xxxx - xxxx
    /^[0-9]{3}-[0-9]{3}-[0-9]{4}$/.test(phoneTxt) || // xxx - xxx - xxxx
    /^[0-9]{3}-[0-9]{4}-[0-9]{4}$/.test(phoneTxt) // xxx - xxxx - xxxx
  );
};

const checkEmail = (emailTxt) => {
  return /^([0-9a-zA-Z_.-]+)@([0-9a-zA-Z_-]+)(\.[0-9a-zA-Z_-]+){1,2}$/.test(emailTxt);
};

export { checkPhoneNum, checkEmail };
